
@mixin videotron-box-shadow {

    --bf-box-shadow-light           : 0 .125rem .5rem 0 rgba(5,4,4, .2); // select, tab
    --bf-box-shadow-medium          : 0 .25rem 1.25rem 0 rgba(5,4,4, .2); // cards
    --bf-box-shadow-strong          : 0 .5rem 1.25rem 0 rgba(5,4,4, .5); // navigation, megamenu, modal
    --bf-box-shadow-strong-inverted : 0 -.5rem 1.25rem 0 rgba(5,4,4, .5); // sticky bottom

}